@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap);
.App{padding-bottom:30vh}@media (max-width: 600px){.App{padding-top:30vh}}

.GridContainer{display:grid}

.Section{display:flex;justify-content:center;align-items:center;padding-top:5%;padding-bottom:5%}.Section>.Section__Contents{max-width:1400px;padding-left:15px;padding-right:15px;width:100%}@media (min-width: 601px){.Section>.Section__Contents{width:80%}}

.RichText>*{margin-top:0;margin-bottom:0}.RichText>*+*{margin-top:10px}.RichText--figcaption{color:gainsboro;font-size:0.875rem;white-space:pre-wrap}.RichText--figcaption>address{font-style:normal;color:darkgray;margin-bottom:5px}.RichText--figcaption>address:last-child{margin-bottom:0}

body{margin:0;font-family:'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:black}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}img{max-width:100%}figure{margin:0}

